.profile {
    color: white;
    font-family: Calibri;
    text-shadow: 1px 1px 10px black;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-direction: column;
        flex-direction: column;
}

h1 {
    margin-bottom: 100px;
    text-align: center;
}

.logout-btn {
    width: 150px;
    height: 40px;
    position: absolute;
    right: 10px;
    top: 10px;
    font-family: Calibri;
    font-size: 25px;
    font-weight: bold;
    color: white;
    background-color: rgb(235, 114, 114);
    border: 1px solid rgb(218, 5, 5);
    -webkit-box-shadow: 1px 1px 10px 1px black;
            box-shadow: 1px 1px 10px 1px black;
    outline: none;
    cursor: pointer;
    border-radius: 4px;
    -webkit-transition: -webkit-box-shadow linear 0.1s, -webkit-transform linear 0.1s, -webkit-box-shadow linear 0.1s;
    transition: -webkit-box-shadow linear 0.1s, -webkit-transform linear 0.1s, -webkit-box-shadow linear 0.1s;
    -o-transition: box-shadow linear 0.1s, transform linear 0.1s, box-shadow linear 0.1s;
    transition: box-shadow linear 0.1s, transform linear 0.1s, box-shadow linear 0.1s;
    transition: box-shadow linear 0.1s, transform linear 0.1s, box-shadow linear 0.1s, -webkit-box-shadow linear 0.1s, -webkit-transform linear 0.1s, -webkit-box-shadow linear 0.1s;
}

.logout-btn:hover {
    -webkit-transform: scale(1.01);
        -ms-transform: scale(1.01);
            transform: scale(1.01);
    border: 1px solid rgb(73, 9, 133);
    -webkit-box-shadow: 1px 1px 13px 1px black;
            box-shadow: 1px 1px 13px 1px black;
    -webkit-transition: -webkit-box-shadow linear 0.1s, -webkit-transform linear 0.1s, -webkit-box-shadow linear 0.1s;
    transition: -webkit-box-shadow linear 0.1s, -webkit-transform linear 0.1s, -webkit-box-shadow linear 0.1s;
    -o-transition: box-shadow linear 0.1s, transform linear 0.1s, box-shadow linear 0.1s;
    transition: box-shadow linear 0.1s, transform linear 0.1s, box-shadow linear 0.1s;
    transition: box-shadow linear 0.1s, transform linear 0.1s, box-shadow linear 0.1s, -webkit-box-shadow linear 0.1s, -webkit-transform linear 0.1s, -webkit-box-shadow linear 0.1s;
}

.logout-btn:active {
    -webkit-transform: scale(0.99);
        -ms-transform: scale(0.99);
            transform: scale(0.99);
    border: 1px solid rgb(73, 9, 133);
    -webkit-box-shadow: 1px 1px 8px 1px black;
            box-shadow: 1px 1px 8px 1px black;
    -webkit-transition: -webkit-box-shadow linear 0.05s, -webkit-transform linear 0.05s, -webkit-box-shadow linear 0.05s;
    transition: -webkit-box-shadow linear 0.05s, -webkit-transform linear 0.05s, -webkit-box-shadow linear 0.05s;
    -o-transition: box-shadow linear 0.05s, transform linear 0.05s, box-shadow linear 0.05s;
    transition: box-shadow linear 0.05s, transform linear 0.05s, box-shadow linear 0.05s;
    transition: box-shadow linear 0.05s, transform linear 0.05s, box-shadow linear 0.05s, -webkit-box-shadow linear 0.05s, -webkit-transform linear 0.05s, -webkit-box-shadow linear 0.05s;
}


