.friend-list {
    display: -ms-flexbox;
    display: flex;
    width: 1200px;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}

.friend-card {
    background-color: rgb(238, 238, 238);
    width: 200px;
    height: 300px;
    border-radius: 5px;
    -webkit-box-shadow: 1px 1px 10px 1px black;
            box-shadow: 1px 1px 10px 1px black;
    text-align: center;
    -webkit-transition: -webkit-box-shadow linear 0.1s,  -webkit-transform linear 0.1s;
    transition: -webkit-box-shadow linear 0.1s,  -webkit-transform linear 0.1s;
    -o-transition: box-shadow linear 0.1s,  transform linear 0.1s;
    transition: box-shadow linear 0.1s,  transform linear 0.1s;
    transition: box-shadow linear 0.1s,  transform linear 0.1s,  -webkit-box-shadow linear 0.1s,  -webkit-transform linear 0.1s;
}

.friend-card:hover {
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
    -webkit-box-shadow: 1px 1px 15px 1px black;
            box-shadow: 1px 1px 15px 1px black;
    -webkit-transition: -webkit-box-shadow linear 0.1s,  -webkit-transform linear 0.1s;
    transition: -webkit-box-shadow linear 0.1s,  -webkit-transform linear 0.1s;
    -o-transition: box-shadow linear 0.1s,  transform linear 0.1s;
    transition: box-shadow linear 0.1s,  transform linear 0.1s;
    transition: box-shadow linear 0.1s,  transform linear 0.1s,  -webkit-box-shadow linear 0.1s,  -webkit-transform linear 0.1s;
}

.friend-card img {
    width: 100%;
    height: 70%;
    -o-object-fit: cover;
       object-fit: cover;
    border-radius: 5px 5px 0 0;
}

.friend-card h2 {
    color: black;
    font-family: Calibri;
    font-weight: bold;
    text-shadow: 0 0 0;
    font-size: 20px;
    margin: 20px 5px 5px 0;
}


