*, *:before, *:after {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

body {
	background: #1c2326;
}

.banter-loader {
	position: absolute;
	left: 50%;
	top: 50%;
	width: 72px;
	height: 72px;
	margin-left: -36px;
	margin-top: -36px;
}

.banter-loader__box {
	float: left;
	position: relative;
	width: 20px;
	height: 20px;
	margin-right: 6px;
}

.banter-loader__box:before {
	content: "";
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	background: #fff;
}

.banter-loader__box:nth-child(3n) {
	margin-right: 0;
	margin-bottom: 6px;
}

.banter-loader__box:nth-child(1):before, .banter-loader__box:nth-child(4):before {
	margin-left: 26px;
}

.banter-loader__box:nth-child(3):before {
	margin-top: 52px;
}

.banter-loader__box:last-child {
	margin-bottom: 0;
}

@-webkit-keyframes moveBox-1 {
	9.09091% {
		-webkit-transform: translate(-26px, 0);
