	}

	54.54545% {
		-webkit-transform: translate(26px, 26px);
		        transform: translate(26px, 26px);
	}

	63.63636% {
		-webkit-transform: translate(26px, 26px);
		        transform: translate(26px, 26px);
	}

	72.72727% {
		-webkit-transform: translate(26px, 0px);
		        transform: translate(26px, 0px);
